<template>
  <a-layout-content style="padding: 0 50px">
    <a-breadcrumb style="margin: 16px 0">
      <a-breadcrumb-item>首页</a-breadcrumb-item>
      <a-breadcrumb-item>扫描网址</a-breadcrumb-item>
    </a-breadcrumb>

    <div
      :style="{
        background: '#fff',
        padding: '24px',
        minHeight: '620px',
        height: '100%',
      }"
    >
      <div class="content-title">
        <a-icon type="edit" :style="{ fontSize: '30px', color: '#08c' }" />
        <span>输入你想检测的网址</span>
      </div>
      <a-input-search
        class="content-input"
        placeholder="http://...."
        v-model="inputUrl"
        enter-button="Search"
        size="large"
        @search="onSearch"
      />
      <div class="content-table-spin">
        <a-spin size="large" v-if="showSpin" />
      </div>
      <div id="content-table">
        <Table v-if="showTable" :url="inputUrl" :data="tableData"></Table>
      </div>
      <div style="margin: 0 auto">
        <img src="../assets/chart.png" alt="图片" v-if="showImg" />
      </div>
    </div>
  </a-layout-content>
</template>

<script>
import Table from "../components/Table";
import axios from "axios";

export default {
  name: "table",
  data() {
    return {
      inputUrl: "",
      showImg: true,
      showTable: false,
      tableData: [],
      showSpin: false,
    };
  },
  components: {
    Table,
  },
  methods: {
    async onSearch() {
      this.showImg = false;
      this.showSpin = true;
      try {
        const { data } = await axios.get("http://47.111.253.248:5000/url/", {
          params: {
            url: this.inputUrl,
          },
        });
        for (let i = 1; i <= 3; i++) {
          let param = "black_list" + i;
          data[param] = data[param] ? "异常" : "未发现异常";
        }
        this.tableData.unshift(data);
        this.showTable = true;
        this.showSpin = false;
        const tempData = JSON.parse(JSON.stringify(data));
        const tempTude = data.tude;
        const tudeArr = tempTude.split(",");
        tempData.tude = tudeArr;
        
        this.$store.commit("setMapState", tempData);
      } catch (error) {
        this.$message.error("This is an error message");
        this.showTable = false;
        this.showImg = true;
        this.showSpin = false;
      }
    },
  },
};
</script>

